import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import {
  Box, Button, Center, Flex, List, Text
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { listAllAccounts } from '../../../../store/middlewares/account';
import { listAllbusiness } from '../../../../store/middlewares/business';
import { updateAccountsByBusiness, updateSelectedAccountDetails, updateSelectedAccountType } from '../../../../store/actionCreators/account';
import { apiErrorHandler, currencyFormat, isCrypto } from '../../../../utils/general';
import { AccountsByBusiness } from '../../../../types/business';
import AccountListItem from '../../../../components/AccountListItem';
import { listAllCryptoAccounts } from '../../../../store/middlewares/crypto';
import { updateSelectedCryptoAccount } from '../../../../store/actionCreators/crypto';
import Icon from '../../../../components/Icon';
import style from './List.module.css';
import RadioButtonGroup from '../../../../components/RadioButton/RadioButtonGroup';

const AccountList: React.FC = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();
  const person = useAppSelector((store) => store.user.personDetails, shallowEqual);
  const [accountsData, updateAccounts] = useState<AccountsByBusiness[]>([]);
  const [isReady, updateReadyState] = useState(false);

  const showAccountTypeSelection = true;

  const programConfig = useAppSelector((store) => store.core.programConfig, shallowEqual);
  const totalAccounts = useAppSelector((store) => store.account.totalAccounts);

  const [accountType, updateAccountType] = useState<any>('cash');

  const fetchAccountsByBusiness = useCallback(() => {
    dispatch(listAllbusiness())
      .then(({ data }) => {
        const businessMap: {
            [id: string]: AccountsByBusiness
        } = {
          personChecking: {
            id: 'personChecking',
            legalName: `${person?.firstName} ${person?.lastName}`,
            // accounts: [],
            totalBalance: 0.00,
            total: 0
          } as AccountsByBusiness
        };
        data.forEach((b) => {
          businessMap[b.id] = {
            ...b,
            accounts: [],
            totalBalance: 0.00,
            total: 0
          };
        });
        if (accountType === 'cash') {
          dispatch(listAllAccounts(null, 'all', 50))
            .then(({ data: accounts }) => {
              accounts.forEach((acc) => {
                const a = { ...acc };
                if (a.businessId === '') {
                  a.businessId = 'personChecking';
                }
                if (businessMap[a.businessId]) {
                  if (businessMap[a.businessId].accounts === undefined) {
                    businessMap[a.businessId].accounts = [];
                  }
                  businessMap[a.businessId].accounts.push(a);
                  businessMap[a.businessId].total += 1;
                  businessMap[a.businessId].totalBalance += parseFloat(a.availableBalance);
                }
              });

              updateAccounts(Object.values(businessMap));
              dispatch(updateAccountsByBusiness(businessMap));
            })
            .catch(apiErrorHandler)
            .finally(() => {
              updateReadyState(true);
            });
        } else if (accountType === 'crypto') {
          dispatch(listAllCryptoAccounts(null, 'all', 50))
            .then(({ data: accounts }) => {
              accounts.forEach((acc) => {
                const a = { ...acc };
                if (a.businessId === '') {
                  a.businessId = 'personChecking';
                }
                if (businessMap[a.businessId]) {
                  if (businessMap[a.businessId].accounts === undefined) {
                    businessMap[a.businessId].accounts = [];
                  }
                  businessMap[a.businessId].accounts.push(a);
                  businessMap[a.businessId].total += 1;
                  businessMap[a.businessId].totalBalance += parseFloat(a.usdBalance);
                }
              });

              updateAccounts(Object.values(businessMap));
              dispatch(updateAccountsByBusiness(businessMap));
            })
            .catch(apiErrorHandler)
            .finally(() => {
              updateReadyState(true);
            });
        }
      }).catch(apiErrorHandler);
  }, [accountType]);

  useEffect(() => {
    fetchAccountsByBusiness();
  }, [accountType]);

  const maxAccounts = useMemo(() => {
    try {
      const personal = parseInt(programConfig?.bank?.maxAccounts?.personal, 10);
      const business = parseInt(programConfig?.bank?.maxAccounts?.business, 10);

      return personal + business;
    } catch {
      return -1; // no limit
    }
  }, [programConfig]);

  return (
    <div className="page">
      <MiniBox
        varient="box"
        showBack
        bodyClass={style.bodyStyles}
        onBack={() => {
          navigate(-1);
        }}
        footer={(maxAccounts === -1 || maxAccounts > totalAccounts) ? (
          <Button
            width="100%"
            variant="primary"
            onClick={() => {
              navigate('/ao/account/setup-options');
            }}
          >
            {t('account_list.buttn_addAccount')}
          </Button>
        ) : null}
      >
        <Box width="100%">
          {showAccountTypeSelection
            ? (
              <Box marginBottom="32px">
                <RadioButtonGroup
                  name="accountType"
                  defaultValue="cash"
                  options={[
                    { value: 'cash', label: 'Cash' },
                    { value: 'crypto', label: 'Crypto' },
                  ]}
                  onChange={updateAccountType}
                />
              </Box>
            ) : null }
          {accountsData.length === 0 && isReady ? (
            <Center py={2}>
              <Text variant="regular" textAlign="center" className="text-light">
                {t('account_list.noData', 'No Account found')}
              </Text>
            </Center>
          )
            : accountsData.map((b, i) => (
              b.legalName !== '' && b.total > 0
                ? (
                  <Box key={b.legalName} paddingTop={i !== 0 ? '30px' : '0'}>
                    <Flex justifyContent="space-between" marginBottom="16px">
                      <Box>
                        <Text variant="regular" fontWeight="500" display="block" textAlign="left">{b.legalName}</Text>
                        <Text variant="small" display="block" textAlign="left">
                          {b.total}
                          {' '}
                          {t('account_list.accounts')}
                        </Text>
                      </Box>
                      <Box>
                        <Text display="block" variant="regular" textAlign="right" className="text-dark">
                          {currencyFormat(b.totalBalance)}
                        </Text>
                        {b.total > 2
                          ? (
                            <Flex
                              alignItems="center"
                              cursor="pointer"
                              justifyContent="flex-end"
                              onClick={() => {
                                navigate(`/ao/account/list-by-business/${b.id}`);
                              }}
                            >
                              <Text textAlign="right" display="block" variant="regular" className="text-light">{t('account_list.viewAll')}</Text>
                              <Icon icon="Arrow-small-right" size={11} style={{ marginLeft: '8px', width: 'auto' }} className="svg-light-grey-1" />
                            </Flex>
                          ) : null }
                      </Box>
                    </Flex>
                    <List>
                      {b.accounts?.slice(0, 2).map((a) => (
                        <AccountListItem
                          key={a.id}
                          accountDetails={a}
                          onSelect={(selectedAccount) => {
                            if (accountType === 'crypto' && isCrypto(selectedAccount)) {
                              dispatch(updateSelectedCryptoAccount(selectedAccount));
                            } else if (!isCrypto(selectedAccount)) {
                              dispatch(updateSelectedAccountDetails(selectedAccount));
                            }
                            dispatch(updateSelectedAccountType(accountType));

                            navigate('/dashboard');
                          }}
                        />
                      ))}
                    </List>
                  </Box>
                ) : null
            ))}
        </Box>
      </MiniBox>
    </div>
  );
};

export default AccountList;
