import React from 'react';
import {
  Box, Text, useRadio, UseRadioProps
} from '@chakra-ui/react';

const RadioButton = ({ children, ...other }: UseRadioProps & React.PropsWithChildren) => {
  const { getInputProps, getCheckboxProps } = useRadio(other);

  const input = getInputProps();
  const checkbox = getCheckboxProps();

  return (
    <Box as="label" width="100%">
      <input {...input} />
      <Box
        {...checkbox}
        cursor="pointer"
        borderWidth="1px"
        borderRadius="md"
        boxShadow="none"
        _checked={{
          bg: 'white',
          color: '#141414',
          borderColor: 'white',
        }}
        _focus={{
          boxShadow: 'none',
        }}
        padding="6px"
      >
        <Text fontSize="14px" fontWeight="500" textAlign="center">
          {children}
        </Text>
      </Box>
    </Box>
  );
};

export default RadioButton;
